import React, {useState, useEffect} from 'react'
import {ethers} from 'ethers';
import Axios from 'axios';
import image from './components/logo_letter.png';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const NavbarWithMetamaskConnect = (props) => {

	const [errorMessage, setErrorMessage] = useState(null);

	const notifyConnected = () => toast("You are now connected");
	const notifyError = () => toast({errorMessage});

	const [navbarStatus, setNavbarStatus] = useState("Connect");

	const [loginStatus, setLoginStatus] = useState(" ");

	const [defaultAccount, setDefaultAccount] = useState(null);
	const [userBalance, setUserBalance] = useState(null);

	const joinedOn = new Date();

	const register = () => {
		Axios.post("https://trellisserver.herokuapp.com/register", {
		  username: defaultAccount,
		  joinedOn: joinedOn
		}).then((response) => {
		  console.log(response);
		});
	  };

	const connectWalletHandler = () => {
		if (window.ethereum && window.ethereum.isMetaMask && navbarStatus === "Connect") {

			window.ethereum.request({ method: 'eth_requestAccounts'})
			.then(result => {
				accountChangedHandler(result[0]);
				setNavbarStatus('Disconnect');
				getAccountBalance(result[0]);
				notifyConnected();
				
			})
			.catch(error => {
				setErrorMessage(error.message);
				notifyError();
			
			});

		} else {
			setNavbarStatus('Connect');
			accountChangedHandler("");
			setErrorMessage('Please install MetaMask browser extension to interact');

		}
	}

	const accountChangedHandler = (newAccount) => {
		setDefaultAccount(newAccount);
		getAccountBalance(newAccount.toString());
	}

	const getAccountBalance = (account) => {
		window.ethereum.request({method: 'eth_getBalance', params: [account, 'latest']})
		.then(balance => {
			setUserBalance(ethers.utils.formatEther(balance));
		})
		.catch(error => {
			setErrorMessage(error.message);
		});
	};

	const chainChangedHandler = () => {
		window.location.reload();
	}


	window.ethereum.on('accountsChanged', accountChangedHandler);
	window.ethereum.on('chainChanged', chainChangedHandler);


  const changeStatus = (defaultAccount) => {
	{ 
	  props.authorizationChild(defaultAccount);
	}
   };

   useEffect(() => {
	changeStatus(defaultAccount);
	   if (defaultAccount) {
     	register(); }
	  }, [defaultAccount]);

	
	return (
	<>
<div className="nav-bar">      
<div className="logo-bottom-top" >
{/*  <img width="70" height="29" src={image}/> */}  trellis
  </div>  
  <div className="logo-bottom"> <br></br> &nbsp;Test Creator</div> 
  <button className="button-wallet"  onClick={connectWalletHandler} >{navbarStatus}</button> 
  <ToastContainer  />

</div>
</>
	);
}

export default NavbarWithMetamaskConnect;