import React, { useState } from 'react';
import ButtonGroup from '@mui/material/ButtonGroup';
import Button from '@mui/material/Button';
import FormControl from '@mui/material/FormControl';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Modal from '@material-ui/core/Modal';
import Box from '@mui/material/Box';
import Axios from 'axios'

const Options = (props) => {

    const username = props.status;
    const [showAccount, setShowAccount] = useState(true);
    const [showNetwork, setShowNetwork] = useState(false);
    const [name, setName] = useState("");

    const [open, setOpen] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
   
    const deleteUser = () => {
      Axios.delete("https://trellisserver.herokuapp.com/deleteuser", { username: username }).then((response) => {
      });
    };

    const updateName = () => {
      Axios.put("https://trellisserver.herokuapp.com/updateTestStatus", {
        name: name,
        username: username           
      })
    }
  

    const style = {
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      width: 200,
      bgcolor: 'background.paper',
      border: '2px solid #000',
      boxShadow: 24,
      p: 4,
    };
   

    const buttons = [
        <Button key="one" onClick={() => { setShowAccount(true); setShowNetwork(false) }}>Account</Button>,
        <Button key="two" onClick={() => { setShowAccount(false); setShowNetwork(true) }}>Tests and Network</Button>,
      ];

      
    return ( <div className="header-profile">

     <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>         
          <Button style={{cursor:"pointer"}} onClick= { deleteUser }>Delete Account</Button>
        
        </Box>
      </Modal>

       <h3>Settings</h3> <br></br>

       <ButtonGroup size="small" aria-label="small button group">
        {buttons}
       </ButtonGroup>

      <br></br> <br></br>
      { showAccount && <>
       <p style={{cursor:"pointer"}} onClick={handleOpen}>Delete Account</p>     
       </>
      }

     { showNetwork && <>

      <FormControl>
   
       <FormControlLabel control={<Checkbox  />} 
       label="Publish results on network after complete test" />
      </FormControl> <br></br> <br></br>
      <Button variant="contained">Save</Button>
      </>
       }
    </div> );
}
 
export default Options;