import React, { useState } from 'react';
import { BrowserRouter, Route, Link, Routes, Navigate} from 'react-router-dom';
import './App.css'
import Navbar from './NavbarMetamask'
import Bottom from './Bottom'
import Options from './Options'
import Profile from './Profile'
import History from './History'
import Welcome from './Welcome'
import Results from './Results'
import { ReactComponent as Settings } from './components/settings.svg';
import { ReactComponent as HomeScreen } from './components/home.svg';
import { ReactComponent as ProfileIcon } from './components/profil2.svg';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button } from '@material-ui/core';
import ButtonGroup from '@mui/material/ButtonGroup';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import SpeedIcon from '@mui/icons-material/Speed';
import ViewColumnIcon from '@mui/icons-material/ViewColumn';
import Segments from './Manager';
import 'bootstrap/dist/css/bootstrap.min.css';

function App() {

const [status, setStatus] = useState("");

function authorizationParent (defaultAccount) {
  setStatus (defaultAccount);
}

  const buttons = [
    <Button key="two"> <Link to="/"><HomeScreen style={{ cursor: 'pointer' }}/></Link></Button>,
    <Button key="one"><Link to="Manager"><ViewColumnIcon color="action" style={{ cursor: 'pointer' }} /></Link></Button>,
    <Button key="three"><Link to="Profile"><ProfileIcon style={{ cursor: 'pointer' }} /></Link></Button>,
    <Button key="four"><Link to="Options"><Settings style={{ cursor: 'pointer' }} /></Link></Button>,
    <Button key="five"><Link to="Launch"><OpenInNewIcon color="action" style={{ cursor: 'pointer' }} /></Link></Button>,
    <Button key="six"><Link to="Results"><SpeedIcon color="action" style={{ cursor: 'pointer' }} /></Link></Button>
  ];

  return (
    <div className="App">

   <Navbar authorizationChild = {authorizationParent} />
  
   <BrowserRouter basename={process.env.PUBLIC_URL}>    
        <div className = "segments">
          
         <Routes>
              <Route path="/" element={<Welcome status = {status} />}/>
              <Route  path='/Options' element= {<Options status = {status} />}/>
              <Route  path='/Manager' element= {<Segments status = {status}  />}/>
              <Route  path='/Profile' element= {<Profile status = {status}  />}/>
              <Route  path='/Launch' element= {<History status = {status}/>}/> 
              <Route  path='/Results' element= {<Results/>}/>                         
        </Routes>

        </div>
        
          <ButtonGroup
            orientation="vertical"
            aria-label="vertical outlined button group"
            position="absolute">
            {buttons}
          </ButtonGroup>   

         </BrowserRouter>         
         <Bottom status = {status}/>       
        </div>
    );
}

export default App;