import React, { useState, useEffect } from 'react';
import Axios from 'axios';
import List from '@mui/material/List';
import Box from '@mui/material/Box';
import ListItemText from '@mui/material/ListItemText';
import Input from '@mui/material/Input'
import { ListItem } from '@mui/material';

const Results = () => {

    const [testResult, setTestResultList] = useState([]);
    const [search, setSearch] = useState("");

    useEffect(() => {
        Axios.post("https://trellisserver.herokuapp.com/getresults").then((response) => {
          setTestResultList(response.data);
        });
      }, [testResult]);

return ( <>

<div className="header-profile">
 
          <h3>Results</h3> 
      
           <div className ="search-bar">
                
                             
             <Input type="text" placeholder="Find..." onChange={(event) => {
                   setSearch(event.target.value);
                     }}> </Input>
                     </div>

                     { testResult.filter((val) => {
                if (search == "") { return val }
                else if (val.loginStatus.toLowerCase().includes(search.toLowerCase())) { return val }
              }).map((value, key) => {
     return (
       <>
          <Box sx={{ width: '66%', borderRadius: '5px' }}>
          <List sx={{
        
            position: 'relative',
            overflow: 'auto',
            maxHeight: 500,
            '& ul': { padding: 0 },
          }}> <ListItem>    
          <ListItemText primary={` TestId: ${value.testId}`}
                        secondary={value.loginStatus}
                                />   
                                <ListItemText secondary={`Score: ${value.score}`} /> 
                                </ListItem>                      
         </List>
         </Box>
       </>
     );
   })}
                    
               </div>


    </> );
}
 
export default Results;