import React,  { useState, useEffect } from 'react';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Tooltip from '@mui/material/Tooltip';
import OpenInNewIcon from '@mui/icons-material/OpenInNew';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import Axios from 'axios';
import Input from '@mui/material/Input';
import { Button } from '@mui/material';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';

const History = (props) => {

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);


  const [search, setSearch] = useState("");
  const [testList, setTestList] = useState([]);


  const [date, setDate] = useState("");


  const [testIdNotification, setTestIdNotification] = useState("");
  const [segmentIdNotification, setSegmentIdNotification] = useState("");

  const [getQuestionForTest, setGetQuestionForTest] = useState([]);

  const [showMinQuestionMessage, setShowMinQuestionMessage] = useState(false);

  const [showConfirmLaunchMessage, setShowConfirmLaunchMessage] = useState(true);

  const [submitEnable, setSubmitEnable] = useState(false);

  const foreignKey = props.status;

  const [testStatusJSON, setTestStatusJSON] = useState({});

  var testParsed = JSON.parse(JSON.stringify(testStatusJSON));
  var testParsedFirst = testParsed[0];

  var testStatus = '';

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };


 useEffect(() => {
    Axios.post("https://trellisserver.herokuapp.com/readSavedTestList", 
    { foreignKey: foreignKey }).then((response) => {
      setTestList(response.data);
    });
    }, [testList]); 

    useEffect(() => {
      Axios.post("https://trellisserver.herokuapp.com/getQuestionsSizeForTest", 
      { testId: testIdNotification }).then((response) => {
        setGetQuestionForTest(response.data);
      });
      }, [getQuestionForTest], [testIdNotification]); 

    useEffect(() => {
      Axios.post("https://trellisserver.herokuapp.com/getStatusForNotification", 
      { segmentIdNotification: segmentIdNotification }).then((response) => {
        setTestStatusJSON(response.data); 
      });
      }, [segmentIdNotification]); 

   async function getStatusForNotification (updatedValue) {

     const response = await Axios.post("https://trellisserver.herokuapp.com/getStatusForNotification", 
        { segmentIdNotification: segmentIdNotification })

        setTestStatusJSON(response.data);  
        handleOpen();   
     
   }

  const launchTest = () => {
    
    if(getQuestionForTest.length > 4) {
      Axios.put("https://trellisserver.herokuapp.com/launchtest", {
        testIdNotification:  testIdNotification,     
        testStatus: 'active'      
      }).then(() => {
        console.log("success")
      });
      handleClose(); }

      else {
setShowMinQuestionMessage(!showMinQuestionMessage);
setShowConfirmLaunchMessage(!showConfirmLaunchMessage)
      }     
    };  

   const notifyCandidates = () => {
      Axios.put("https://trellisserver.herokuapp.com/notifyCandidates", {
        segmentIdNotification:segmentIdNotification,
        testStatus: testStatus }
        ).then((response) => {
     
       console.log('success')
      });
    }; 


    return ( 

      <div className="header-profile">

       <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
       
        <Box sx={style}>
        { showConfirmLaunchMessage && <>
        <input  type="checkbox"
              onChange={(e) => setSubmitEnable(!submitEnable)}
            /> &nbsp; Confirm test launch
      <br></br> <br></br>
      { submitEnable && <>
      <Button variant= "contained" 
        onClick={() => launchTest() }>Go</Button>
        </>
      }  </> }


{ showMinQuestionMessage && <>
Minimum 5 question required to launch a test
</>}


      </Box> 
     

        </Modal>

         <h3>Quick Launch</h3> 
     
          <div className ="search-bar">
                            
            <Input type="text" placeholder="Find..." onChange={(event) => {
                  setSearch(event.target.value);
                    }}> </Input>
                    </div>
                    <>

                      { testList.filter((val) => {
                        if (search == "") { return val }
                        else if (val.testName.toLowerCase().includes(search.toLowerCase())) { return val }
                        }).map((val, key) => {
                        return (
                          <List sx={{ width: '33%', bgcolor: 'background.paper' }}>
          
                         <br></br>
                                   <ListItem
                                   key={val}
                                   disableGutters
                                   secondaryAction={
                                    <Tooltip title="Launch test">
                                     <IconButton>
                                       <OpenInNewIcon 
                                       onClick={() => 
                                        { setTestIdNotification(val.testId); 
                                          setSegmentIdNotification(val.segmentId);                          
                                          handleOpen()}}/>
                                     </IconButton>
                                     </Tooltip>
                                   }
                                 >
                                   <ListItemText primary= {val.testName}  />
                                   <ListItemText secondary={val.testId} />
                                 </ListItem>
                               </List>
                       );
                      })}
                    </>
              </div>
     );
  }

export default History;